import React from "react";
import "./App.css";
import EventList from "./EventList";
import CitySearch from "./CitySearch";
import EventCount from "./EventCount";
import { getEvents } from "./api";

class App extends React.Component {
  state = {
    events: [],
    page: 5,
    lat: 0,
    lon: 0
  };

  componentDidMount() {
    getEvents().then(events => this.setState({ events }));
  }

  componentDidUpdate(prevProps, prevState) {
    // only update chart if the data has changed

    console.log("REBB this props", this.props);
    console.log("REBB prevProps", prevProps);

    console.log("REBB this state", this.state);
    console.log("REBB prevStaet", prevState);
    console.log("REBB *********");
    // if (prevProps.data !== this.props.data) {
    //   this.chart = c3.load({
    //     data: this.props.data
    //   });
    // }
    if (this.state.page !== prevState.page) {
      this.updateEvents(this.state.page, this.state.lat, this.state.lon);
    }
  }

  updateEvents = (page, lat, lon) => {
    getEvents(page, lat, lon).then(events =>
      this.setState({ events, lat, lon })
    );
  };

  updateCount = page => {
    const { lat, lon } = this.state;
    this.setState({ page }, () => {
      // console.log("REBB lat", lat);
      // console.log("REBB lon", lon);
    });
  };

  render() {
    // There is no need to make another API request if
    // const eventsDisplay =
    //   this.state.events.length < this.state.page
    //     ? this.state.events
    //     : this.state.events.slice(0, this.state.page);
    console.log("REBB render state", this.state);
    return (
      <div className="App">
        TEST10
        <CitySearch updateEvents={this.updateEvents} page={this.state.page} />
        <EventCount updateCount={this.updateCount} />
        <EventList events={this.state.events} />
      </div>
    );
  }
}

export default App;
